.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 254, 253, 0.8);
  backdrop-filter: blur(8px);
  align-items: center;
  justify-content: center;
  z-index: 99;

  display: none;
}

.popup--filters {
  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    top: 180px;
    left: 98px;
    width: 210px;
    box-shadow: none;
    background: transparent;
    padding: 0;

    max-height: 850px;
  }
}

.popup--opened {
  display: flex;
}

.popup__success {
  display: flex;
}

.popup__toggle {
  position: absolute;
  top: 20px;
  right: 20px;

  display: block;
  width: 22px;
  height: 22px;
  background-color: $white;
  cursor: pointer;
  border: none;
}

.popup__toggle--catalog {
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.popup__toggle {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 8px;
    right: 0;
    width: 21px;
    height: 2px;
    background-color: $text-color;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: $text-color;
  }
}


