.offer {
  padding: 0 15px;

  @media (min-width: $tablet-width) {
    padding: 0 45px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 98px;
  }
}

.offer__inner {
  background-image: url("../img/offer-bg-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  border-radius: 8px;
  min-height: 496px;

  padding-top: 50px;
  margin-bottom: 70px;

  @media (min-width: $tablet-width) {
    background-image: url("../img/offer-bg-tablet@1x.png");
    min-height: 359px;
    padding-left: 50px;
    margin-bottom: 120px;
    padding-top: 70px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/offer-bg-desktop@1x.png");
    min-height: 379px;
    padding-left: 270px;
    padding-top: 80px;
  }
}

.offer__title {
  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;

  margin: 0;
  text-align: center;

  & span {
    display: block;
    font-size: 37px;
    line-height: 44px;

    @media (min-width: $tablet-width) {
      font-size: 56px;
      line-height: 66px;
    }
  }

  @media (min-width: $tablet-width) {
    text-align: left;
    font-size: 36px;
    line-height: 43px;
  }

}

.offer__conditions {
  font-size: 15px;
  line-height: 140%;
  margin: 0;
  padding: 20px 25px 30px;
  text-align: center;

  @media (min-width: $tablet-width) {
    text-align: left;
    width: 239px;
    padding: 25px 0 35px 0;
  }
}

.offer__btn {
  width: 206px;
  padding: 13px 25px 13px 0;
  margin: 0 auto;
  border: none;

  @media (min-width: $tablet-width) {
    margin-left: 0;
  }
}
