.contacts {
  background-color: $orange;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 369px 1fr;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 568px 1fr;
  }
}

.contacts__wrapper {
  padding: 40px 15px 20px;

  @media (min-width: $tablet-width) {
    padding: 60px 45px 40px;
  }

  @media (min-width: $desktop-width) {
    padding: 70px 111px 50px 198px;
  }
}

.contacts__title {
  margin: 0;
  padding-bottom: 40px;
  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 43px;
  }
}

.contacts__item {
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  margin: 0;

  & span {
    display: block;
    font-weight: normal;
    padding-top: 10px;
    padding-bottom: 30px;
    max-width: 200px;
  }

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 19px;

    & span {
      max-width: 220px;
      padding-bottom: 35px;
    }
  }
}

.contacts__social--footer {
  padding-top: 15px;
}

.contacts__map {
  height: 300px;
  width: 100%;
  padding: 0;
  margin: 0;
  background-image: url("../img/bg-map-mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 1;

  @media (min-width: $tablet-width) {
    height: 100%;
    background-image: url("../img/bg-map-tablet.png");
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/bg-map-desktop.png");
  }
}
