.header {
  background-color: $white;

  @media (min-width: $tablet-width) {
    background-color: $white;
  }
}

.header__menu {
  padding: 12px 15px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 45px;
  }

  @media (min-width: $desktop-width) {
    padding: 12px 98px;
  }
}

.header__logo-image {
  @media (min-width: $tablet-width) {
    width: 63px;
    height: 52px;
  }
}
