.rooms {
  padding: 0 15px;
  position: relative;

  & >input {
    display: none;
  }

  @media (min-width: $tablet-width) {
    padding: 0 45px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 168px;
  }
}

.rooms--room-page {
  margin-bottom: 50px;
}

.rooms::before {
  content: "";
  position: absolute;
  background-image: url("../img/fish.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 150px;
  height: 46px;
  left: 0;
  top: 25px;
  z-index: -1;

  @media (min-width: $tablet-width) {
    background-image: url("../img/fish-tablet.png");
    width: 339px;
    height: 105.76px;
  }

  @media (min-width: $desktop-width) {
    width: 484px;
    height: 151px;
    top: 20px;
  }
}

.rooms__slider {
  position: relative;
  min-height: 490px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    min-height: 346px;
  }

  @media (min-width: $desktop-width) {
    min-height: 450px;
  }
}

.rooms__inner {
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;

  position: absolute;
  z-index: 0;
  opacity: 0;
  transition: transform ease-in-out .5s, opacity ease-in-out .5s;
  width: 100%;

  & picture {
    justify-self: center;
  }

  @media (min-width: $tablet-width) {
    background-color: $white;
    box-shadow: none;
    width: auto;
    margin-left: calc(50% - 282px);
  }

  @media (min-width: $desktop-width) {
    margin-left: calc(50% - 515px);
  }
}

.rooms__description-list {
  @include list-reset;

  @media (min-width: $tablet-width) {
    padding-left: 25px;
  }
}

.rooms__description-item {
  font-size: 15px;
  line-height: 18px;
  padding-bottom: 17px;
}

.rooms__description-item::before {
  background-color: $orange;
  content: "";
  width: 11px;
  height: 11px;
  padding-right: 17px;
  margin-right: 8px;
  border-radius: 50%;
}

.rooms__title {
  text-align: center;

  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;

  margin-top: 40px;
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 70px;
    margin-top: 110px;
  }
}

.rooms__img {
  width: 290px;
  height: auto;
  margin-left: calc(50% - 145px);

  @media (min-width: $tablet-width) {
    width: 442px;
    position: relative;
    object-fit: cover;
    height: 326px;
  }

  @media (min-width: $desktop-width) {
    width: 600px;
    margin-left: 0;
    height: 404px;
  }

}

.rooms__subtitle {
  margin: 25px;
  font-family: $rubik;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    margin-bottom: 15px;
    margin-top: 15px;
  }
}

.rooms___button {
  position: relative;
  max-width: 191px;
  margin-left: 0;
  padding: 16px 49px 16px 0;

  @media (min-width: $tablet-width) {
    max-width: 157px;
    margin-left: 25px;
    padding: 16px 49px 16px 6px;
  }
}

.rooms__container {
  padding-bottom: 20px;
  margin-left: calc(50% - 145px);

  @media (min-width: $tablet-width) {
    position: absolute;
    width: 290px;
    top: 9%;
    left: 100%;
    background-color: #fff;
    box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
    border-radius: 8px;
    padding: 15px 40px 40px 15px;
    margin-left: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 100px 40px 75px;
    left: 530px;
    top: 13%;
    min-width: 308px;
  }
}
