.reviews {
  padding: 0 15px;
  position: relative;
  overflow: hidden;
  margin-bottom: 70px;

  & >input {
    display: none;
  }

  @media (min-width: $tablet-width) {
    padding-left: 45px;
    padding-right: 0;
    margin-bottom: 120px;
    overflow: hidden;
    width: 100%;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 152px;
  }
}

.reviews::before {
  content: "";
  position: absolute;
  background-image: url("../img/paw-2.png");
  width: 112px;
  height: 95px;
  transform: matrix(-0.79, 0.62, 0.62, 0.79, 0, 0);
  right: -25px;
  top: 55px;

  @media (min-width: $tablet-width) {
    width: 227px;
    height: 194px;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    right: -25px;
    transform: matrix(-0.79, 0.62, 0.62, 0.79, 0, 0);
    top: 65px;
  }

  @media (min-width: $desktop-width) {
    width: 312px;
    height: 267px;
    top: 45px;
  }
}

.reviews__title {
  margin: 70px 0 40px;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-weight: bold;
    font-size: 36px;
    line-height: 43px;

    margin-top: 120px;
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 70px;
    margin-bottom: 70px;
  }
}

.reviews__slider {
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;
  position: relative;
  min-height: 180px;
  overflow: hidden;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    display: flex;
    background-color: $white;
    box-shadow: none;
    border-radius: 0;
    min-height: 201px;
    box-sizing: border-box;
    width: 1386px;
    overflow: auto;
    padding-left: 45px;
    padding-top: 2px;
    padding-bottom: 50px;
    margin-bottom: 0;
    z-index: -1;
  }

  @media (min-width: $desktop-width) {
    min-width: 1648px;
    padding-left: 98px;
    padding-right: 20px;
    padding-bottom: 55px;
  }
}

.reviews__item {
  position: absolute;
  z-index: 0;
  opacity: 0;
  transition: transform ease-in-out .5s, opacity ease-in-out .5s;

  @media (min-width: $tablet-width) {
    position: relative;
    display: flex;
    z-index: 1;
    opacity: 1;
    margin-right: 30px;
    background-color: #fff;
    box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
    border-radius: 8px;
  }

  @media (min-width: $desktop-width) {
    min-width: 470px;


    &:last-child {
      margin-right: 0;;
    }
  }
}
