.footer__container {
  padding: 30px 15px;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 45px;
  }

  @media (min-width: $desktop-width) {
    padding: 12px 198px;
  }
}

.footer__copyright {
  border-top: 1px solid #EFEFEF;
  padding: 15px;
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    padding: 20px 45px;
  }

  @media (min-width: $desktop-width) {
    padding: 20px 198px;
  }
}

.footer__logo-image {
  @media (min-width: $tablet-width) {
    width: 63px;
    height: 52px;
  }
}
