.room {
  padding: 0 15px;
  position: relative;

  @media (min-width: $tablet-width) {
    padding: 0 45px;
    padding-top: 60px;
    padding-bottom: 100px;
    display: flex;
    flex-wrap: nowrap;
  }

  @media (min-width: $desktop-width) {
    position: relative;
    overflow: hidden;
    padding: 0 98px;
    padding-top: 70px;
  }
}

@media (min-width: $desktop-width) {
  .room::before {
    content: "";
    position: absolute;
    right: -75px;
    width: 251px;
    height: 215px;
    background-image: url("../img/paw-2.png");
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    transform: matrix(0.71, 0.71, 0.71, -0.71, 0, 0);
  }
}


.room__image-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;

  @media (min-width: $tablet-width) {
    width: 50%;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 70px auto;
    grid-template-rows: repeat(3, 1fr);
    gap: 30px;
  }

}

.room__img-main {
  grid-column-start: 1;
  grid-column-end: 4;

  background-image: url("../img/room-6-mobile@2x.jpg");
  width: 100%;
  min-height: 288px;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 8px;

  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    grid-row: 1/4;
  }
}

.room__image {
  border-radius: 4px;
  width: 86px;
  height: 64px;
  background-image: url("../img/room-6-mobile@2x.jpg");

  @media (min-width: $desktop-width) {
    width: 70px;
    height: 112px;
  }
}

.room__image--preview-1 {
  justify-self: right;
}

.room__image--preview-2 {
  justify-self: center;
}

.room__image--preview-3 {
  justify-self: left;
}

.room__content {
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  margin-left: calc(50% - 145px);

  @media (min-width: $tablet-width) {
    width: calc(50% - 59px);
    margin-left: 59px;
  }

  @media (min-width: $desktop-width) {
    width: calc(50% - 70px);
    margin-left: 70px;
    font-size: 16px;
    line-height: 19px;
  }
}

.room__title {
  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;

  margin: 0;
  padding: 30px 0;

  @media (min-width: $tablet-width) {
    padding-top: 0;
    padding-bottom: 15px;
  }

  @media (min-width: $desktop-width) {
    font-weight: bold;
    font-size: 36px;
    line-height: 43px;
  }
}

.room__description {
  margin: 0;
  padding-bottom: 15px;

  @media (min-width: $tablet-width) {
    padding-bottom: 10px;
  }
}

.room__price {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}

.room__equipment-list {
  @include list-reset;

  & span {
    display: block;
    padding-bottom: 15px;
  }
}

.room__equipment-item {
  padding-bottom: 15px;
}

.room__icon {
  padding-right: 10px;
}

.room__button {
  position: relative;
  padding: 16px 0 16px 0;
  font-size: 16px;
  line-height: 19px;
  width: 290px;
  margin-top: 10px;

  border: none;

  @media (min-width: $tablet-width) {
    margin-left: 0;
    width: 206px;
    text-align: left;
    padding-left: 30px;
  }
}

