.promo-block {
  text-align: center;
  min-height: 457px;

  background-image: url("../img/cat-mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;

  padding-top: 50px;

  @media (min-width: $tablet-width) {
    background-image: url("../img/cat-tablet.png");
    min-height: 868px;

    padding-top: 140px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/cat-desktop.png");
    min-height: 504px;
    text-align: left;
    padding-top: 153px;
    padding-left: 198px;
  }
}

.promo-block__title {
  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 44px;
  line-height: 52px;

  margin: 0;
  padding-bottom: 12px;

  @media (min-width: $tablet-width) {
    padding-bottom: 25px;
    font-size: 62px;
    line-height: 73px;
  }
}

.promo-block__description {
  margin: 0;

  font-weight: 500;
  font-size: 16px;
  line-height: 19px;

  padding-bottom: 40px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 24px;
  }
}

.promo-block__city-title {
  margin: 0;

  font-size: 12px;
  line-height: 14px;

  padding-bottom: 12px;

  @media (min-width: $tablet-width) {
    padding-bottom: 15px;
    font-size: 14px;
    line-height: 17px;
  }

}

.promo-block__button {
  position: relative;
  width: 206px;
  margin: 0 auto;
  box-shadow: 0px 5px 25px rgba(26, 33, 47, 0.1);
  border-radius: 25px;
  padding: 13px 25px 13px 0;
  text-align: left;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}
