.advantages {
  padding: 0 15px;
  position: relative;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    padding: 0 45px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 98px;
  }
}

.advantages::before {
  content: "";
  position: absolute;
  background-image: url("../img/paw-2.png");
  width: 116px;
  height: 99px;
  transform: matrix(-0.93, -0.36, -0.36, 0.93, 0, 0);
  right: -25px;
  top: 27px;

  @media (min-width: $tablet-width) {
    width: 300px;
    height: 257px;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
    right: -80px;
  }

  @media (min-width: $desktop-width) {
    width: 367px;
    height: 314px;
  }
}

.advantages__title {
  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
  text-align: center;

  margin-top: 70px;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    margin-top: 120px;
    margin-bottom: 60px;
    font-size: 36px;
    line-height: 43px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 70px;
  }
}

.advantages__list {
  @include list-reset;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item {
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 8px;

  padding: 25px;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    min-height: 285px;
  }

  @media (min-width: $desktop-width) {
    padding: 30px;
    min-height: 321px;
  }
}

.advantages__img {
  @media (min-width: $tablet-width) {
    width: 75px;
    height: 75px;
  }

  @media (min-width: $desktop-width) {
    width: 91px;
    height: 91px;
  }
}

.advantages__subtitile {
  margin: 0;
  padding-top: 30px;
  padding-bottom: 20px;

  font-family: $rubik;
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;

  @media (min-width: $desktop-width) {
    font-size: 22px;
  }
}

.advantages__description {
  margin: 0;

  font-size: 15px;
  line-height: 140%;

  @media (min-width: $desktop-width) {
    font-size: 16px;
  }
}

.advantages__item--temperature::before {
  content: "";
  width: 60px;
  height: 60px;
}
