.button {
  position: relative;
  display: block;
  background-color: $orange-btn;
  color: $white;
  font-size: 16px;
  line-height: 19px;
  text-decoration: none;
  text-align: center;
  box-shadow: 0px 5px 25px rgba(26, 33, 47, 0.1);
  border-radius: 25px;
  outline: none;
}

.button--white {
  background-color: $white;
  color: $text-color;
}

.button::after {
  content: "";
  position: absolute;
  width: 36px;
  height: 36px;
}

.button--paw-white {
  &:hover {
  background-color: $orange-btn;
  color: $white;
  box-shadow: 0px 5px 25px rgba(26, 33, 47, 0.2);

    &.button--paw-white::after {
      background-image: url("../img/paw-white.png");
    }
  }
}

.button--paw-white::after {
  background-image: url("../img/paw.png");
  top: 4px;
  right: 3px;
}

.button--paw-orange {
  &:hover {
  background-color: white;
  color: $text-color;
  box-shadow: 0px 5px 25px rgba(26, 33, 47, 0.2);

    &.room-card__button::after {
      background-image: url("../img/paw.png");
    }
  }
}

.button--paw-orange::after {
  background-image: url("../img/paw-white.png");
  top: 7px;
  right: 7px;
}
