.room-card {
  background: #FFFFFF;
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 6px;
  border-bottom: 1px solid #E1E1E1;
}

.room-card__content {
  border: 1px solid #E1E1E1;
  border-bottom: none;
  border-top: none;
  margin-top: -4px;
  padding-bottom: 25px;
}

.room-card__image-wrapper {
  border: none;
}

.room-card__image {
  width: 100%;
  border-radius: 6px 6px 0px 0px;
}

.room-card__title {
  font-family: $rubik;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;

  margin: 0;
  padding-top: 25px;
  padding-left: 25px;
}

.room-card__description {
  padding: 20px 25px;
  margin: 0;

  font-size: 14px;
  line-height: 17px;

  & span {
    display: block;
    padding-bottom: 10px;
  }

  .room-card__price {
    display: inline;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 20px;
    padding-right: 0;
  }
}

.room-card__button {
  position: relative;
  margin: 0 10px;
  padding: 16px 81px 16px 58px;
  font-size: 14px;
  line-height: 17px;

  @media (min-width: $tablet-width) {
    margin: 0 25px;
  }

  @media (min-width: $desktop-width) {
    min-width: auto;
    max-width: 230px;
    margin: 0 20px;
  }
}

.room-card__equipment {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;

  & span {
    padding-right: 12px;
  }
}

.room-card__features {
  @include list-reset;

  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}

.room-card__feature {
  display: inline-block;
  overflow: hidden;

  width: 18px;
  height: 18px;

  font-size: 0;

  background-position: center;
  background-repeat: no-repeat;

  margin-right: 12px;

  &:last-child {
    margin-right: 0;
  }
}

.room-card__feature--empty {
  background-image: url("../img/icon-empty.svg");
}

.room-card__feature--play {
  background-image: url("../img/icon-play.svg");
}

.room-card__feature--house {
  background-image: url("../img/icon-home.svg");
}

.room-card__feature--ball {
  background-image: url("../img/icon-ball.svg");
}

.room-card__feature--wc {
  background-image: url("../img/icon-wc.svg");
}

