.site-nav {
  position: relative;
}

.site-nav__list {
  @include list-reset;
  padding-top: 25px;

  @media (min-width: $tablet-width) {
    display: flex;
    padding-top: 0;
  }
}

.site-nav__item {
  padding: 24px 0;

  @media (min-width: $tablet-width) {
    padding: 20px 30px  20px 0;

    &:last-child {
      padding-right: 0;
    }
  }

}

.site-nav__link {
  font-family: "Rubik", "Arial", sans-serif;
  text-decoration: none;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: $text-color;

  position: relative;

  &:hover {
    color: orange;
  }

  @media (min-width: $tablet-width) {
    font-family: "Lato", "Arial", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
  }
}

.site-nav__toggle {
  position: absolute;
  top: -35px;
  right: 0;

  margin-right: 20px;

  display: block;
  width: 22px;
  height: 22px;
  background-color: $white;
  cursor: pointer;
  border: none;
  outline: none;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

  .site-nav--closed .site-nav__toggle::before {
    content: "";

    position: absolute;
    top: 5px;
    left: 0;

    width: 22px;
    height: 2px;

    background-color: $text-color;
    box-shadow: 0 8px 0 0 $text-color, 0 16px 0 0 $text-color;
  }

  .site-nav--closed .site-nav__toggle:active::before {
    background-color: $text-color;
    box-shadow: 0 8px 0  $text-color, 0 16px 0 0 $text-color;
  }

  .site-nav--opened .site-nav__toggle {
    &::before,
    &::after {
      content: "";

      position: absolute;
      top: 15px;
      right: 0;
      width: 21px;
      height: 2px;
      background-color: $text-color;
    }

    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }

    &::after {
      transform: rotate(-45deg);
    }

    &:active::before,
    &:active::after {
      background-color: $text-color;
    }
  }

  @media (max-width: $mobile-width-only) {
    .site-nav--closed .site-nav__list {
      display: none;
    }

    .site-nav--closed .site-nav__social {
      display: none;
    }

    .site-nav--closed .site-nav__phone {
      display: none;
    }
  }

  .site-nav--nojs .site-nav__toggle {
    display: none;
  }

  .site-nav__phone {
    font-family: "Rubik";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;

    margin: 25px 0;
  }

  @media (min-width: $tablet-width) {
    .site-nav__phone,
    .site-nav__social {
      display: none;
    }
  }

  .site-nav__link--footer {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    font-family: $lato;
  }

  .site-nav__list--footer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 15px;

    @media (min-width: $tablet-width) {
      flex-wrap: nowrap;
      padding-top: 0;
    }
  }

  .site-nav__item--footer {
    padding: 10px 0;
    width: 50%;

    @media (min-width: $tablet-width) {
      width: auto;
      padding-right: 30px;
      font-size: 16px;
      line-height: 19px;
    }
  }


