.review-card::before {
  content: "";
  position: absolute;
  background-image: url("../img/quotes.png");
  background-repeat: no-repeat;
  width: 21px;
  height: 42px;
  left: 25px;
  top: 25px;
}

.review-card__wrapper {
  margin: 0;
  padding: 25px 21px 25px 56px;

  @media (min-width: $tablet-width) {
    padding: 35px 33px 35px 70px;
  }
}

.review-card__text {
  margin: 0;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }
}

.review-card__footer {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    padding-top: 25px;
    font-size: 14px;
    line-height: 17px;
    color: #848484;
  }
}

.review-card__date {
  margin: 0;
}

.review-card__author {
  font-style: normal;
}
