.order-popup {
  display: none;
  background: #FFFEFD;
}

.order-popup__block {
  background-color: #fff;
  max-width: 290px;
  position: relative;
  box-sizing: border-box;
  padding: 60px 15px;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 10px;

  @media (min-width: $tablet-width) {
    max-width: 502px;
    padding: 60px 30px;
  }

  @media (min-width: $desktop-width) {
    max-width: 570px;
    padding: 70px 70px;
  }
}

.order-popup__title {
  margin: 0;
  text-align: center;
  padding: 0 50px 40px;

  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
}

.order-popup__fieldset {
  padding: 0;
  margin: 0;
  border: none;
}

.order-popup__input {
  max-width: 260px;
  border: 1px solid #D7D7D7;
  box-sizing: border-box;
  border-radius: 25px;
  font-size: 15px;
  line-height: 18px;
  color: #1A212F;
  width: 100%;

  padding: 15px;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    max-width: 100%;
  }
}

.order-popup__label {
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    width: 25%;
  }
}

.order-popup__date {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: nowrap;
    align-items: baseline;
  }
}

.order-popup__date-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: baseline;
  margin-top: 15px;

  & .order-popup__input {
    width: 107px;

    @media (min-width: $tablet-width) {
      width: 132px;
    }
  }

  & span {
    margin-right: 5px;
  }
}

.order-popup__date-wrap .order-popup__input {
  margin-right: 10px;
  text-align: center;

  &:last-child {
    margin-right: 0;
  }
}

.order-popup__btn {
  width: 100%;
  padding: 15px 0;
  padding-left: 47px;
  text-align: left;
  border: none;

  @media (min-width: $tablet-width) {
    width: 230px;
    margin: 0 auto;
    margin-top: 40px;
  }
}