.catalog {
  padding: 0 15px;
  padding-top: 40px;
  position: relative;

  @media (min-width: $tablet-width) {
    padding: 0 45px;
    padding-top: 60px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: baseline;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 245px 1fr;
    grid-template-rows: auto 1fr;

    padding-top: 70px;
    padding-left: 98px;
    padding-right: 98px;

    min-height: 1200px;
  }
}

.catalog__item {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.catalog__list {
  margin-top: 30px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
    margin-bottom: 120px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    row-gap: 30px;

    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 5;
  }

  @media (min-width: $desktop-width) {
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 2;
    grid-column-end: 3;

    grid-template-columns: repeat(3, 1fr);
  }
}

.catalog__title {
  margin: 0;
  padding-bottom: 40px;
  font-family: $rubik;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;

  @media (min-width: $tablet-width) {
    grid-column-start: 1;
    grid-column-end: 3;
    font-size: 36px;
    line-height: 43px;
    padding-bottom: 50px;
  }

  @media (min-width: $desktop-width) {
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

.catalog__btn {
  position: relative;

  height: 35px;

  background: #FFFFFF;
  box-shadow: 0px 2px 12px rgba(173, 152, 143, 0.18);
  border-radius: 25px;
  border: none;

  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  padding: 0;
  padding-left: 25px;

  cursor: pointer;
  outline: none;

  @media (min-width: $tablet-width) {
    justify-self: start;
  }
}

.catalog__btn--sort {
  width: 220px;
  text-align: left;
  font-weight: normal;

  @media (min-width: $desktop-width) {
    justify-self: end;
  }
}

.catalog__btn--filter {
  margin-right: 15px;
  width: 220px;
  text-align: left;
  padding-left: 40px;

  @media (min-width: $tablet-width) {
    margin-left: 15px;
    width: 121px;
  }

  @media (min-width: $desktop-width) {
    grid-row-start: 2;
    display: none;
  }
}

.catalog__btn--filter::before {
  content: "";
  position: absolute;
  background-image: url("../img/icon-filter.svg");
  width: 12px;
  height: 15px;
  left: 20px;
}

.catalog__btn--sort::before {
  content: "";
  position: absolute;
  background-image: url("../img/icon-up.svg");
  background-repeat: no-repeat;
  width: 7px;
  height: 12px;
  left: 35px;
  top: 10px;
}

.catalog__btn--sort::after {
  content: "";
  position: absolute;
  background-image: url("../img/icon-sort.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 10px;
  right: 15px;
  top: 13px;
}

.catalog__empty {
  margin: 0;
  padding: 20px 0;
  font-family: "Rubik", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.catalog__empty-item {
  @media (min-width: $tablet-width) {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  @media (min-width: $desktop-width) {
    grid-column-start: 1;
    grid-column-end: 4;
  }
}
