.filters__block {
  background-color: #fff;
  max-width: 290px;
  position: relative;
  padding: 50px 53px 50px 25px;
  box-shadow: 0px 0px 25px rgba(208, 208, 208, 0.25);
  border-radius: 10px;

  overflow: auto;


  @media (min-width: $tablet-width) {
    max-width: 332px;
    padding: 30px 60px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    box-shadow: none;
  }
}

.filters__fieldset {
  border: none;
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}

.filters__label {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}

.filters__legend {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;

  margin: 0;
  padding-bottom: 15px;
}

.filters__input-wrapper {
  padding-top: 20px;
}

.filters__input {
  border: 1px solid #E1E1E1;
  box-sizing: border-box;
  width: 99px;
  padding: 8px 8px;

  font-size: 14px;
  line-height: 17px;

  &:first-child {
    margin-right: 22px;

    @media (min-width: $desktop-width) {
      margin-right: 10px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 75px;
  }
}

.filters__checkbox {
  margin: 15px 35px;
  margin-right: 0;
}

.filters__btn-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.filters__btn {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  padding: 15px 0;
  margin-right: 25px;
  border-radius: 25px;
  border: none;

  width: 100%;
  outline: none;
  cursor: pointer;
}

.filters__btn--submit {
  margin-bottom: 15px;
  background-color: $orange;

  &:hover {
    background-color: $orange-btn;
  }
}

.filters__btn--reset {
  background-color: transparent;
  border: 2px solid $orange;

  &:hover {
    background-color: $orange-btn;
    opacity: 0.8;
    border: none;
  }
}
