.copyright {
  font-size: 12px;
  line-height: 14px;
  color: #8F8F8F;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
  }
}

.copyright__link {
  text-decoration: none;
  color: #8F8F8F;
}

.copyright__text {
  padding-bottom: 20px;
  margin: 0;

  @media (min-width: $tablet-width) {
    padding-bottom: 0;
  }
}
