.photos {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  user-select: none;
  background-color: #fff;
  width: 290px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 354px;
    margin-right: 0;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    width: 600px;


    grid-template-columns: 70px auto;
    grid-template-rows: repeat(3, 1fr);
    gap: 30px;
  }
}

.photos__back, .photos__item {
  width: 100%;
  height: 288px;
  max-height: 80vh;

  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    grid-row: 1/4;
    height: 396px;
  }
}

.photos__radio {display: none;}

.photos__img {
  width: 100%;
  height: 100%;
  object-fit:cover;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5));
  border-radius: 4px;
}

.photos__img--preview {
  width: 86px;
  height: 64px;

  @media (min-width: $desktop-width) {
    width: 70px;
    height: 112px;
  }
}

.photos__item {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.5s;
}

.photos__view {
  cursor: pointer;
  width: 1px;
  height: 64px;
  max-height: 20vh;
  flex-grow: 1;
  padding-top: 16px;
  text-align: center ;

  @media (min-width: $tablet-width) {
    padding-top: 21px;
  }

}

.photos__img_item {
  object-fit: cover;

  @media (min-width: $tablet-width) {
    border-radius: 8px;
  }
}


.photos__radio:checked+.photos__item {z-index: 1; opacity: 1;}
.photos__radio:checked+.photos__item+.photos__view {transform: scale(.93);}
